<template>
  <ion-page>
    <ion-header mode="ios">
      <ion-toolbar>
        <ion-title>Pengajuan Izin</ion-title>
        <ion-back-button slot="start" default-href="/" />
      </ion-toolbar>
    </ion-header>
    <ion-content :fullscreen="true">
      <ion-refresher slot="fixed" @ionRefresh="doRefresh($event)">
        <ion-refresher-content />
      </ion-refresher>
      <ion-content class="ion-padding">
        <ion-item>
          <ion-label>Nama Lengkap</ion-label>
          <ion-input v-model="fullName" readonly style="color: red"></ion-input>
        </ion-item>
        <ion-item>
          <ion-label>Role</ion-label>
          <ion-input v-model="role" readonly style="color: red"></ion-input>
        </ion-item>
        <ion-item>
          <ion-label>Status Izin</ion-label>
          <ion-select v-model="izinStatus" placeholder="Pilih Status">
            <ion-select-option value="izin">Izin</ion-select-option>
            <ion-select-option value="sakit">Sakit</ion-select-option>
          </ion-select>
        </ion-item>
        <ion-item>
          <ion-label>Durasi Izin (hari)</ion-label>
          <ion-input
            v-model="durasi"
            type="number"
            placeholder="Masukkan durasi izin"
          ></ion-input>
        </ion-item>
        <ion-item>
          <ion-label>Dari Tanggal</ion-label>
          <ion-button
            style="color: white"
            expand="block"
            @click="openStartDatePicker"
          >
            {{ startDate ? formatDate(startDate) : "Pilih Tanggal" }}
          </ion-button>
        </ion-item>
        <ion-item>
          <ion-label>Sampai Tanggal</ion-label>
          <ion-button
            style="color: white"
            expand="block"
            @click="openEndDatePicker"
          >
            {{ endDate ? formatDate(endDate) : "Pilih Tanggal" }}
          </ion-button>
        </ion-item>
        <ion-item>
          <ion-label>Keterangan</ion-label>
          <ion-textarea
            v-model="keterangan"
            placeholder="Masukkan keterangan"
          ></ion-textarea>
        </ion-item>
        <ion-button style="color: white" expand="block" @click="ajukanIzin"
          >Ajukan Izin</ion-button
        >
        <ion-grid>
          <ion-row>
            <ion-col>
              <h2>Daftar Izin</h2>
              <ion-list>
                <ion-item v-for="izin in daftarIzin" :key="izin.id">
                  <ion-label>
                    <h3 style="color: red">
                      {{ izin.fullName }} - {{ izin.role }}
                    </h3>
                    <p style="color: red">Status: {{ izin.izinStatus }}</p>
                    <p style="color: red">Durasi: {{ izin.durasi }} hari</p>
                    <p style="color: red">Keterangan: {{ izin.keterangan }}</p>
                    <p style="color: red">Status Admin: {{ izin.status }}</p>
                    <p style="color: red">
                      Dari: {{ formatDate(izin.startDate) }}
                    </p>
                    <p style="color: red">
                      Sampai: {{ formatDate(izin.endDate) }}
                    </p>
                    <p style="color: red">
                      Tanggal: {{ formatDate(izin.createdAt) }}
                    </p>
                  </ion-label>
                  <ion-button color="danger" @click="batalkanIzin(izin.id)"
                    >Batalkan Izin</ion-button
                  >
                </ion-item>
              </ion-list>
            </ion-col>
          </ion-row>
        </ion-grid>
      </ion-content>

      <ion-modal
        :is-open="isStartDatePickerOpen"
        @ion-modal-did-dismiss="closeStartDatePicker"
      >
        <ion-content>
          <ion-datetime
            display-format="DD/MM/YYYY"
            picker-format="DD/MM/YYYY"
            v-model="startDate"
            @ionChange="closeStartDatePicker"
          ></ion-datetime>
          <ion-button expand="block" @click="closeStartDatePicker"
            >Tutup</ion-button
          >
        </ion-content>
      </ion-modal>

      <ion-modal
        :is-open="isEndDatePickerOpen"
        @ion-modal-did-dismiss="closeEndDatePicker"
      >
        <ion-content>
          <ion-datetime
            display-format="DD/MM/YYYY"
            picker-format="DD/MM/YYYY"
            v-model="endDate"
            @ionChange="closeEndDatePicker"
          ></ion-datetime>
          <ion-button expand="block" @click="closeEndDatePicker"
            >Tutup</ion-button
          >
        </ion-content>
      </ion-modal>

      <ion-toast
        :is-open="toast.isOpen"
        :message="toast.message"
        :duration="toast.duration"
        :color="toast.color"
        @did-dismiss="() => (toast.isOpen = false)"
      ></ion-toast>
    </ion-content>
  </ion-page>
</template>

<script setup>
import {
  IonPage,
  IonHeader,
  IonToolbar,
  IonTitle,
  IonBackButton,
  IonContent,
  IonRefresher,
  IonRefresherContent,
  IonItem,
  IonLabel,
  IonSelect,
  IonSelectOption,
  IonInput,
  IonTextarea,
  IonButton,
  IonList,
  IonToast,
  IonCol,
  IonRow,
  IonGrid,
  IonModal,
  IonDatetime,
} from "@ionic/vue";
import { ref, onMounted } from "vue";
import { getAuth, onAuthStateChanged } from "firebase/auth";
import {
  getFirestore,
  doc,
  setDoc,
  serverTimestamp,
  getDoc,
  collection,
  query,
  where,
  getDocs,
  deleteDoc,
} from "firebase/firestore";
import { firebaseApp } from "../firebase.js";

const auth = getAuth(firebaseApp);
const db = getFirestore(firebaseApp);

const fullName = ref("");
const role = ref("");
const izinStatus = ref("");
const durasi = ref("");
const keterangan = ref("");
const startDate = ref("");
const endDate = ref("");
const daftarIzin = ref([]);
const toast = ref({
  isOpen: false,
  message: "",
  duration: 2000,
  color: "success",
});
const isStartDatePickerOpen = ref(false);
const isEndDatePickerOpen = ref(false);

const openStartDatePicker = () => {
  isStartDatePickerOpen.value = true;
};

const closeStartDatePicker = () => {
  isStartDatePickerOpen.value = false;
};

const openEndDatePicker = () => {
  isEndDatePickerOpen.value = true;
};

const closeEndDatePicker = () => {
  isEndDatePickerOpen.value = false;
};

const fetchUserData = async (uid) => {
  try {
    const userDoc = await getDoc(doc(db, "users", uid));
    if (userDoc.exists()) {
      const userData = userDoc.data();
      fullName.value = userData.fullName;
      role.value = userData.role;
    } else {
      console.error("User data not found");
      showToast("Data pengguna tidak ditemukan", "danger");
    }
  } catch (error) {
    console.error("Error fetching user data:", error);
    showToast("Terjadi kesalahan saat mengambil data pengguna", "danger");
  }
};

const fetchIzinData = async (uid) => {
  const q = query(collection(db, "izin"), where("uid", "==", uid));
  try {
    const querySnapshot = await getDocs(q);
    daftarIzin.value = querySnapshot.docs.map((doc) => ({
      id: doc.id,
      ...doc.data(),
    }));
    console.log("Data izin berhasil diambil:", daftarIzin.value);
  } catch (error) {
    console.error("Error fetching izin data:", error);
    showToast("Terjadi kesalahan saat mengambil data izin", "danger");
  }
};

const ajukanIzin = async () => {
  const user = auth.currentUser;
  if (user) {
    const izinData = {
      uid: user.uid,
      fullName: fullName.value,
      role: role.value,
      izinStatus: izinStatus.value,
      durasi: durasi.value,
      keterangan: keterangan.value,
      startDate: startDate.value ? new Date(startDate.value) : null,
      endDate: endDate.value ? new Date(endDate.value) : null,
      createdAt: serverTimestamp(),
      status: "pending", // Default status pending, admin will approve or reject later
    };
    try {
      await setDoc(
        doc(db, "izin", user.uid + "_" + new Date().getTime()),
        izinData
      );
      showToast("Pengajuan izin berhasil diajukan", "success");
      izinStatus.value = "";
      durasi.value = "";
      keterangan.value = "";
      startDate.value = "";
      endDate.value = "";
      fetchIzinData(user.uid); // Fetch updated izin data
    } catch (error) {
      console.error("Error submitting izin:", error);
      showToast("Terjadi kesalahan saat mengajukan izin", "danger");
    }
  } else {
    console.error("User not authenticated");
    showToast("Pengguna tidak terautentikasi", "danger");
  }
};

const batalkanIzin = async (izinId) => {
  try {
    await deleteDoc(doc(db, "izin", izinId));
    showToast("Izin berhasil dibatalkan", "success");
    const user = auth.currentUser;
    if (user) {
      fetchIzinData(user.uid); // Refresh the izin data after deletion
    }
  } catch (error) {
    console.error("Error deleting izin:", error);
    showToast("Terjadi kesalahan saat membatalkan izin", "danger");
  }
};

const doRefresh = (event) => {
  setTimeout(() => {
    const user = auth.currentUser;
    if (user) {
      fetchIzinData(user.uid);
    }
    event.target.complete();
  }, 2000);
};

const showToast = (message, color) => {
  toast.value = {
    isOpen: true,
    message: message,
    duration: 2000,
    color: color,
  };
};

const formatDate = (timestamp) => {
  const date = timestamp
    ? timestamp.toDate
      ? timestamp.toDate()
      : new Date(timestamp)
    : null;
  return date ? date.toLocaleDateString("id-ID") : "N/A";
};

onMounted(() => {
  onAuthStateChanged(auth, (user) => {
    if (user) {
      fetchUserData(user.uid);
      fetchIzinData(user.uid);
    } else {
      console.error("User not authenticated");
      showToast("Pengguna tidak terautentikasi", "danger");
    }
  });
});
</script>

<style>
/* Custom styling if needed */
</style>
